import React from 'react'
import RandomLetters from '../RandomLetters'
import MediaQuery from 'react-responsive'

const Science = () => (
  <div className="scene-container">
    <div className="jumbotron">
      <div className="container">
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-12 animation-1">
            <h1><RandomLetters label="Science"/></h1>
          </div>
        </div>
        <MediaQuery query="(max-device-width: 1024px)">
          <div className="row grid-x grid-margin-x">
            <ul className="tabs" data-tabs id="science-tabs">
              <li className="tabs-title is-active"><a href="#panel1" aria-selected="true">1.</a></li>
              <li className="tabs-title"><a data-tabs-target="panel2" href="#panel2">2.</a></li>
              <li className="tabs-title"><a data-tabs-target="panel3" href="#panel3">3.</a></li>
              <li className="tabs-title"><a data-tabs-target="panel4" href="#panel4">4.</a></li>
            </ul>
          </div>
        </MediaQuery>
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-4 animation-1">
            <p>Over the course of two hours, Boutrous ran through the findings of the most recent IPCC report (released
              in 2013), acknowledging that global temperatures were rising due to carbon dioxide caused by human
              activity, that other factors were negligible, and that as a result, sea levels were rising. No time was
              given to any denials of climate science, with Boutrous sticking closely to the substance of the IPCC
              report. Even his attempt at implying that his client was not at fault was framed within</p>
          </div>
          <div className="col cell medium-4 animation-2">
            <p>he said that the report never said extraction or production of oil was the cause of carbon dioxide
              emissions, but rather the economic activity that burned fossil fuels. Judge William Alsup, who is
              presiding over the lawsuit, is giving the other oil companies  ExxonMobil, ConocoPhillips, Royal Dutch
              Shell, and BP  a week to quibble with anything Boutrous said in court about climate change. Chevron
              welcomes meaningful efforts to address the issue of climate change, but litigation</p>
          </div>
          <div className="col cell medium-4 animation-3">
            <p>Wednesdays session was not a trial, or really even a hearing in the typical sense of the word. It was,
              in Judge Alsups terminology, a tutorial  a session in which the legal teams for both sides present
              background facts for his edification. Its not discovery, its not evidence, and just because it was
              presented Wednesday, its not necessarily admissible for a jury in the future should this case go to
              trial. </p>
          </div>
        </div>
      </div>
    </div>
  </div>
)

export default Science



