import React from 'react'

const BG = () => (
  <div className="scene-container">
    <div className="jumbotron">
      <div className="container">
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-12 animation-1">
            <h1>BG</h1>
          </div>
        </div>
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-4 animation-1">
            <h2 style={{color: '#BD1E1B'}}>We are working on the development of such algorithms and solutions.</h2>
          </div>
          <div className="col cell medium-4 animation-2">
            <h2>Modern&nbsp;<br className="show-for-medium show-for-large"/>
              M2C.</h2>
            <div className="separator-40"/>
            <p>M2C interfaces have learned to give relevant answers in terms of meaning and emotions. This is based on
              technologies of speech processing (Natural-language processing. </p>
          </div>
          <div className="col cell medium-4 animation-3">
            <h2>Truly&nbsp;<br className="show-for-medium show-for-large"/>
              AI.</h2>
            <div className="separator-40"/>
            <p>However, AI truly communicates with the user only when it will be able to understand the social context
              and psycho-emotional state of a person.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
)

export default BG



