import React from 'react'

const Mission = () => (
  <div className="scene-container">
    <div className="jumbotron">
      <div className="container">
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-12 animation-1">
            <h1>Mission</h1>
            <p className="lead"><span className="first-sentence">We are on the bright side.</span> <span
              style={{color: '#ababab'}}>We create solutions that make a person's communication with AI comfortable and truly personal.</span>
            </p>
            <div className="separator-40"/>
          </div>
          <div className="col cell medium-2">
          </div>
        </div>
        <div className="row grid-x grid-margin-x">
          <p className="col cell medium-5 animation-2">Our algorithms will enhance peoples chance to efficiently take
            decisions and communicate in the new world - the world of AI driven algorithms.
          </p>
          <p className="col cell medium-5 animation-3">We believe that a person is the only owner of his data and has to
            be able getting useful insights from them.
          </p>
          <p className="col cell medium-2">
          </p>
        </div>
      </div>
    </div>
  </div>
)

export default Mission



