import React, {Component} from 'react'
import {Fullpage, Slide} from './fullpage-react'
import ReactGA from 'react-ga'
import './App.css'
import Menu from './components/Menu'
import Contact from './components/Contact'
import Team from './components/Team'
import Context from './components/Context'
import Products from './components/Products'
import Science from './components/Science'
import BG from './components/BG'
import How from './components/How'

import Main from './components/Main'
import Mission from './components/Mission'
// import Scene3D from './components/Scene3D'
//import SceneComp from './components/SceneComp'

if (process.env.GA_TRACKING_ID) {
  ReactGA.initialize(process.env.GA_TRACKING_ID, {
    debug: true,
    titleCase: false
  })
}

const fullPageOptions = {
  // for mouse/wheel events
  // represents the level of force required to generate a slide change on non-mobile, 10 is default
  scrollSensitivity: 7,
  
  // for touchStart/touchEnd/mobile scrolling
  // represents the level of force required to generate a slide change on mobile, 10 is default
  touchSensitivity: 7,
  scrollSpeed: 1,
  hideScrollBars: true,
  enableArrowKeys: true,
  activeSlide: 0
}

const names = [
  {className: 'main', backgroundColor: '#042a85'},
  {className: 'mission', backgroundColor: '#1e1e1e'},
  {className: 'bg', backgroundColor: '#fc2622'},
  {className: 'how', backgroundColor: '#042a85'},
  {className: 'context', backgroundColor: '#042a85'},
  {className: 'science', backgroundColor: '#1e1e1e'},
  {className: 'products', isLight: true, backgroundColor: '#ffffff'},
  {className: 'team', backgroundColor: '#1e1e1e'},
  {className: 'contacts', backgroundColor: '#fc2622'}
]

const slides = [
  <Slide className="hq-slide">
    <Main/>
  </Slide>,
  <Slide className="hq-slide">
    <Mission/>
  </Slide>,
  <Slide className="hq-slide">
    <BG/>
  </Slide>,
  <Slide className="hq-slide">
    <How/>
  </Slide>,
  <Slide className="hq-slide">
    <Context/>
  </Slide>,
  <Slide className="hq-slide">
    <Science/>
  </Slide>,
  <Slide className="hq-slide">
    <Products/>
  </Slide>,
  <Slide className="hq-slide">
    <Team/>
  </Slide>,
  <Slide className="hq-slide">
    <Contact/>
  </Slide>
]

fullPageOptions.slides = slides

class App extends Component {
  state = {
    active: {
      Fullpage: 0, data: names[0]
    }
  }
  
  componentDidMount() {
    /*
    setTimeout(() => {
      if (this.state.active.Fullpage === 0) {
        this.changeFullpageSlide(1)
      }
    }, 5000)
    */
    
    this.lastSlideChangeTime = Date.now()
    requestAnimationFrame(() => document.querySelectorAll('.hq-slide')[this.state.active.Fullpage].classList.add('active-slide'))
  }
  
  onSlideChangeStart = (name, props, state, newState) => {
    // since we use very small fullPageOptions.scrollSpeed value, it is easy to scroll
    // past the slide to counter that we shall require minimum slide time here
    let time = Date.now()
    if (time - this.lastSlideChangeTime < 1000) {
      return true
    }
    if (state.scrollPending) {
      this.lastSlideChangeTime = time
    }
    
    document.body.classList.add('body-animation')
    
    const prev = names[newState.lastActive]
    const next = names[newState.activeSlide]
    
    if (prev) {
      document.body.classList.remove('body-' + prev.className)
    }
    if (next) {
      document.body.classList.add('body-' + next.className)
    }
    
    if (next.isLight) {
      document.body.classList.add('body-light')
    } else {
      document.body.classList.remove('body-light')
    }
    
  }
  
  onSlideChangeEnd = (name, props, state, newState) => {
    if (newState.activeSlide == newState.lastActive) return
    
    document.body.classList.remove('body-animation')
    this.setState({active: {Fullpage: newState.activeSlide, data: names[newState.activeSlide]}})
    
    let i, slides = document.querySelectorAll('.hq-slide')
    for (i = 0; i < slides.length; i++) {
      slides[i].classList[i == newState.activeSlide ? 'add' : 'remove']('active-slide')
    }
  }
  
  changeFullpageSlide = (index) => {
    Fullpage.changeFullpageSlide(index)
  }
  
  render() {
    
    const scope = this
    const {active} = this.state
    
    //return (
    //  <div>
    //    <Menu changeFullpageSlide={this.changeFullpageSlide} active={active.Fullpage}/>
    //    <SceneComp backgroundColor={active.data ? active.data.backgroundColor: '#000000'}/>
    return (
      <div>
        <Menu changeFullpageSlide={this.changeFullpageSlide} active={active.Fullpage}/>
        <Fullpage {...fullPageOptions}
                  onSlideChangeStart={this.onSlideChangeStart}
                  onSlideChangeEnd={this.onSlideChangeEnd}
        />
        <nav>
          <ul className="pagination"
              style={{
                position: 'fixed',
                right: 40,
                top: '50%',
                transform: 'translateY(-50%)',
                display: 'flex',
                flexDirection: 'column'
              }}>
            {slides.map((item, index) => (
              <li className={`page-item ${index === active.Fullpage ? 'active current' : ''}`}
                  key={`page-item-${index}`}>
                <a className="page-link"
                   onClick={() => this.changeFullpageSlide(index)}
                   style={{border: 0, borderRadius: 0, backgroundColor: 'transparent', textIndent: '-10000px'}}
                   data-tooltip
                >
                  {index + 1}
                </a>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    )
  }
}

export default App



