import React, {Component} from 'react'
import {ScrollBox, FastTrack} from 'react-scroll-box'
import MediaQuery from 'react-responsive'
import 'react-scroll-box/target/out/index.css'
import RandomLetters from '../RandomLetters'

const menuItems = ['The Model', 'Digital Freud', 'True', 'InQ', 'Deep Persona']

const itemDescriptions = [
  'Some lorem ipsum about the model menu item yep it is, baby, it is',
  'Digital feud is probably when two hackers compete for the hardware for their botnets',
  'The project (application) enables a person to see himself through other people eyes, to appreciate the digital charisma of his friends and contacts',
  'InQ? No idea, I think it used to be peer to peer in previous version of this slide. Well, they are going to provide some text for this.',
  'Deep down every person is, in fact, a fish. Search your feelings, you\'ll know it to be true.'
]

const itemPictures = [
  require('../../images/product-logo-1.svg'),
  require('../../images/product-logo-2.svg'),
  require('../../images/product-logo-3.svg'),
  require('../../images/product-logo-4.svg'),
  require('../../images/product-logo-5.svg')
]

class Products extends Component {
  state = {
    active: 2
  }
  
  render() {
    return (
      <div className="scene-container">
        <div className="jumbotron">
          <div className="container">
            <div className="row grid-x grid-margin-x">
              <div className="col cell medium-12 animation-1">
                <h1><RandomLetters label="Products"/></h1>
              </div>
            </div>
            <div className="row grid-x grid-margin-x">
              <div className="col cell medium-4 animation-2">
                <MediaQuery query="(max-device-width: 1024px)">
                  <ScrollBox disableScrollY hideScrollBarX style={{height: '63px'}}>
                    <ul className="list-group list-group-flush no-bullet submenu">
                      {menuItems.map((item, index) => (
                        <li key={'menu-item-' + index}
                            onClick={() => this.setState({active: index})}
                            className={this.state.active == index ? 'list-group-item submenu-item active' : 'submenu-item list-group-item'}>{item}.</li>
                      ))}
                    </ul>
                  </ScrollBox>
                </MediaQuery>
                <MediaQuery query="(min-device-width: 1024px)">
                  <ul className="list-group list-group-flush no-bullet">
                    {menuItems.map((item, index) => (
                      <li key={'menu-item-' + index}
                          onClick={() => this.setState({active: index})}
                          className={this.state.active == index ? 'list-group-item active' : 'list-group-item'}>{item}.</li>
                    ))}
                  </ul>
                </MediaQuery>
              </div>
              <div className="col cell medium-4 item-descriptions animation-3">
                {itemDescriptions.map((item, index) => (
                  <div key={'item-description-' + index}
                       className={this.state.active == index ? 'item-description active' : 'item-description'}>
                    <h3>{menuItems[index]}.</h3>
                    <div className="separator-40"></div>
                    <p>{item}</p>
                  </div>
                ))}
              </div>
              <div className="col cell medium-4 animation-4">
                <img src={itemPictures[this.state.active]}></img>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Products


