import React from 'react'
import RandomLetters from '../RandomLetters'

const Context = () => (
  <div className="scene-container">
    <div className="jumbotron">
      <div className="container">
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-12 animation-1">
            <h1><RandomLetters label="Context"/></h1>
            <p className="lead">A modern smartphone contains the most complete and up-to-date information about behavior
              and persons social activity.</p>
            <div className="separator-40"/>
          </div>
          <div className="col cell medium-2">
          </div>
        </div>
        <div className="row grid-x grid-margin-x">
          <p className="col cell medium-5 animation-2">The revolution in the social sciences, connected with the ability
            to collect and analyze such data, has just begun: digital psychometry, digital linguistics, sociology and
            anthropology.
          </p>
          <p className="col cell medium-5 animation-3">We have developed a framework that allows us to analyze data on
            social behavior and determine the social signature of a person.
          </p>
          <p className="col cell medium-2">
          </p>
        </div>
      </div>
    </div>
  </div>
)

export default Context



